/* styles.css */

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
}

.gradient-text {
  background: -webkit-linear-gradient(#ee4b4b, #ca0202);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 40px;
  margin: 0;
  border: none;
  width: auto;
  height: auto;
  line-height: normal;
  display: inline-block;
  font-weight: bold;
  letter-spacing: 4px;
  word-spacing: 4px;
  text-align: center;
  text-transform: uppercase;
  font-size: 2em;
  overflow: hidden;
  white-space: nowrap;
  text-shadow: 1px 1px 2px rgba(0,0,0,.7), inset 1px
  1px 1px 2px rgba(255,255,255,.9);
  box-decoration-break: clone;
}
/*css para la barra de busqueda*/
.search-bar {
  text-align: center; /* Centra el contenido horizontalmente */
  margin-top: 50px; /* Agrega margen superior para separar de la parte superior */
  margin-bottom: 50px;
}

.search-bar input {
  padding: 5px;
  margin-right: 10px;
  width: 400px; /* Ajusta el ancho según tus preferencias */
  height: 30px;
  border-radius: 10px;
}

/*css para las categorias*/
@property --rotate{
  syntax: "<angle>";
  initial-value: 132deg;
  inherits: false;
}
:root{
  --card-height:50vh;
  --card-width: 30vh;
}
.category-container{
  margin:0;
  background-image: transparent;
  display: flex;
  align-items:center;
  flex-wrap: wrap;
  gap: 1rem;
  }
  .categorias{
    position: relative;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
    cursor: pointer;
    box-shadow: 0 0 20px rgba(0,0,0,.5);
    color: white;
}
/* Estilo para alinear la lista de elementos a la izquierda */
.item-list {
  text-align: left;
  list-style-type: none; /* Elimina los puntos de la lista */
  padding: 5px 5px;
  width: 140px;
  margin: 0;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}
/* Estilo para los elementos individuales en la lista */
.item-list li {
  margin-left: 0; /* Elimina cualquier margen izquierdo predeterminado */
  border-color:#40f1f1;
  border-style: groove;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0);
  padding: 3px;
  margin-bottom: 10px;
  cursor: pointer;
  box-shadow: inset 0 0 0 1px rgb(90, 160, 25),
  inset 0 0 0 1px rgb(90, 160, 255);
  transition: all 0.2s ease-out;
  }
  .item-list li:hover {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: inset 0 0 0 1px rgb(90, 160, 25
    ),
    inset 0 0 0 1px rgb(90, 160, 255);
    transition: all 0.2s ease-out;
  }
/*css para los Juegos*/
.juegos{
  color: white;
  text-align: center;
  min-width: 50%;
}

.juego-list{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.juego-card{
  text-align: center;
  min-width: 325px;
  max-width: 325px;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.2);
}
.juego-card img{
  width: 310px;
  height: 310px;
}
.juego-card p{
  font-size: 30px;
}
.Window-game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
/*css para las subcategorias*/
.subcategorias {
  list-style-type: none;
  padding-left: 5px;
  margin-top: 5px;
}

.subcategorias li {
  cursor: pointer;
}

.subcategorias li:hover {
  color: rgb(138, 14, 14); /* Cambia el color al pasar el ratón por encima */

} 
